import { useEffect, useContext } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { InputBox, PasswordBox, ButtonBox, CheckBox, Buttons, SelectBox, MultiSelectBox } from "../../../components/RenderFroms";
import { useFetch } from "../../../contexts/useFetch";
import { useFetchSingle } from "../../../contexts/useFetchSingle";
import { usePost } from "../../../contexts/usePost";
import { usePatch } from "../../../contexts/usePatch";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";
const resource = "system_users_groups";

const defaultValue = {
	group_name: "",
	modules_name: "",
	Add_permission: "",
	edit_permission: "",
	delete_permission: "",
	sequence_permission: "",
	export_permission: ""

}

export default function AddUsersGroup() {
	const navigate = useNavigate();
	const { id } = useParams();

	const validationSchemaInfo = Yup.object().shape({
		group_name: Yup.string().required("User namr is required"),
		modules_name: Yup.string().required("Modules is required"),
		Add_permission: Yup.string().required('This is required'),
		edit_permission: Yup.string().required('This is required'),
		delete_permission: Yup.string().required('This is required'),
		sequence_permission: Yup.string().required('This is required'),
		export_permission: Yup.string().required('This is required'),


	});

	const { fetchSingle, data: users, loading: loadingData } = useFetchSingle();
	useEffect(() => {
		fetchSingle(resource, id)
	}, [id])

	const { create, data: respond, loading } = usePost();
	const { edit, data: respond1, loading: loading1 } = usePatch();
	const handleUpdate = (values: any) => {
		if (id) {
			edit(resource, values)
		} else {
			create(resource, values)
		}
	}

	useEffect(() => {
		if ((respond as any)?.status == 201) {
			navigate(-1)
		}
		if ((respond1 as any)?.status == 200) {
			navigate(-1)
		}
	}, [respond, respond1])

	const { data: groups } = useFetch({ url: "system_users_groups", query: JSON.stringify({}) });
	let groupList = groups?.data ? groups?.data.map((item: any) => {
		return { label: item.systemUserGroupName, value: item.id }
	}) : []
	const showRecordOptions = [{ label: '25', value: '25' }, { label: '50', value: '50' }, { label: '100', value: '100' }, { label: 'All', value: 'all' }]
	const menuStyleOptions = [{ label: 'Collapsed', value: 'collapsed' }, { label: 'Expanded', value: 'expanded' }]

	if (loadingData) {
		return <>loading</>
	}

	return (
		<>
			<Breadcrumbs root="" pageName="Add Users Group" />

			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">

							<ListTitle ListName={`${id ? 'Edit' : 'Add'} User (* indicates mandatory field(s)).`} />

							<div className="form-horizontal has-validation-callback">
								<Formik
									initialValues={users ? users : defaultValue}
									validationSchema={validationSchemaInfo}
									onSubmit={(values: any) => handleUpdate(values)}
								>
									{({ handleChange, handleBlur, handleSubmit, values, errors }) => (
										<>
											<div className="card-body">

												<SelectBox
													required={true}
													options={groupList}
													name="systemUserGroupId"
													lable="System User Group"
													placeholder="Select Group"
												/>

												<InputBox
													required={true}
													name="systemUserLoginId"
													lable="System User Login ID"
												/>

												<InputBox
													required={true}
													name="systemUserName"
													lable="System User Name"
												/>

												<SelectBox
													required={true}
													options={menuStyleOptions}
													name="systemUserMenuStyle"
													placeholder="Select Menu Style"
													lable="Menu Style"
												/>

												<SelectBox
													required={true}
													options={showRecordOptions}
													name="systemUserShowNoOfRecordsPerPage"
													placeholder="Select No"
													lable="Show No. of Records"
												/>


												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<strong className="red">NOTE: Supply Passwords below only if you want to change.</strong>
													</div>
												</div>

												<PasswordBox
													required={true}
													name="systemUserLoginPassword"
													lable="Login Password"
												/>

												<PasswordBox
													required={true}
													name="confirm_password"
													lable="Confirm Login Password"
												/>

												<div className="clear"></div>
											</div>
											<div className="card-action">
												<div className="form-group">
													<label className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
													<div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
														<Buttons loading={loading || loading1} className="btn-success" value="Save User" onClick={() => handleSubmit()} />
														<Buttons className="btn-grey" value="Go Back to List" onClick={() => navigate(-1)} />
													</div>
												</div>
												<div className="clear"></div>
											</div>
										</>)}
								</Formik>
							</div>
						</div>
					</div>
				</div>
			</div>


		</>
	);
}


